<template>
    <div class="progress-bar" ref="progressBar" @click='clickProgress'>
      <div class="bar-inner">
        <div class="progress" ref="progress"></div>
        <div class="progress-btn-wrapper" ref="progressBtn"
             @touchstart.prevent='touchStart'
             @touchmove.prevent='touchMove'
             @touchend='touchEnd'
        >
          <div class="progress-btn"></div>
        </div>
      </div>
    </div>
</template>

<script>
import {prefixStyle} from "common/js/dom"
const BTN_WIDTH = 16
const transform = prefixStyle('transform')
export default {
    props: {
        percent: {
            type: Number,
            default: 0
        }
    },
    created(){
        this.touch = {}
    },
    methods: {
        touchStart(e){
            this.touch.initiated = true
            this.touch.startX = e.touches[0].pageX
            this.touch.left = this.$refs.progress.clientWidth
        },
        touchMove(e){
            if(!this.touch.initiated){
                return
            }
            const deltaX = e.touches[0].pageX - this.touch.startX
            const offseWidth = Math.min(this.$refs.progressBar.clientWidth - BTN_WIDTH,Math.max(0, this.touch.left+deltaX))
            this._offset(offseWidth)
        },
        touchEnd(){
            this._triggerPercent()
            this.touch.initiated =  false
        },
        clickProgress(e){
          const rect = this.$refs.progressBar.getBoundingClientRect()
          const offsetWidth = e.pageX - rect.left
          this._offset(offsetWidth)
          // this._offset(e.offsetX)
          this._triggerPercent()
        },
        _triggerPercent(percent){
            this.$emit('changeProgress', this._getPercent())
        },
        _getPercent(){
            const barWidth = this.$refs.progressBar.clientWidth - BTN_WIDTH
            return this.$refs.progress.clientWidth/barWidth
        },
        _offset(offsetWidth){
            this.$refs.progress.style.width = `${offsetWidth}px`
            this.$refs.progressBtn.style[transform] = `translate3d(${offsetWidth}px,0,0)`
        },
        setProgressOffset(percent){
            if(percent >= 0 && !this.touch.initiated){
                let barWidth = this.$refs.progressBar.clientWidth - BTN_WIDTH
                let offsetWidth = percent*barWidth
                this._offset(offsetWidth)
            }
        }
    },
    watch: {
        percent(newPercent){
            this.setProgressOffset(newPercent)
        }
    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .progress-bar
      height: 30px
      .bar-inner
        position: relative
        top: 13px
        height: 4px
        background: rgba(0, 0, 0, 0.3)
        .progress
          position: absolute
          height: 100%
          background: $color-theme
        .progress-btn-wrapper
          position: absolute
          left: -8px
          top: -13px
          width: 30px
          height: 30px
          .progress-btn
            position: relative
            top: 7px
            left: 7px
            box-sizing: border-box
            width: 16px
            height: 16px
            border: 3px solid $color-text
            border-radius: 50%
            background: $color-theme
</style>



